@import '../untils/variables';

.logbook {
  display: grid;
  grid-template-columns: 300px 2fr 460px;
  grid-template-rows: auto 1fr;
  min-height: 100vh;
  background: #fbfbfb;

  &__filter {
    grid-column: 1/2;
    grid-row: 1/2;
  }

  &__calendar {
    grid-column: 2/4;
    grid-row: 1/2;
    margin: 10px;

  }

  &__table {
    grid-column: 1/4;
    grid-row: 2/3;
    margin: 10px;
  }

  &__info {
    display: none;
    grid-column: 3/4;
    grid-row: 1/3;
    margin: 10px 0;
    background: #fff;
    border: solid #e4e4e4;
    border-width: 1px 0 1px 1px;
    box-shadow: 0 0 30px $logbook-info-color;
    border-radius: 10px 0 0 10px;

    &--show {
      display: flex;
      z-index: 2;
    }
  }
}
