.form {
  display: flex;
  flex-wrap: wrap;
  align-items: self-start;
  height: 100%;
  padding: 20px 10px 10px;

  &__fieldset {
    flex-shrink: 0;
    padding: 0 10px;

    &--full {
      width: 100%;
    }

    &--half {
      width: 50%;
    }
  }

  &__footer {
    flex-shrink: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin: auto 0 0;
    padding: 10px 0;
  }
}
