.calendar-wrapper {
  display: flex;
}

.calendar {
  margin-right: 10px;
}

#inlineDatePicker2 .datepicker-controls .next-btn,
#inlineDatePicker1 .datepicker-controls .next-btn {
  display: none;
}

#inlineDatePicker2 .datepicker-controls .prev-btn,
#inlineDatePicker3 .datepicker-controls .prev-btn {
  display: none;
}

#inlineDatePicker1 .datepicker-cell.focused:not(.selected),
#inlineDatePicker1 .datepicker-cell.highlighted:not(.selected):not(.range):not(.today).focused {
  background: none;
}

#inlineDatePicker3 .datepicker-cell.focused:not(.selected),
#inlineDatePicker2 .datepicker-cell.highlighted:not(.selected):not(.range):not(.today).focused {
  background: none;
}

#inlineDatePicker1 .datepicker-cell.selected,
#inlineDatePicker1 .datepicker-cell.selected:hover,
#inlineDatePicker1 .datepicker-cell.selected:hover,
#inlineDatePicker3 .datepicker-cell.selected,
#inlineDatePicker3 .datepicker-cell.selected:hover,
#inlineDatePicker3 .datepicker-cell.selected:hover{
  background: none;
  color: #000000;
  font-weight: 400;
}

.datepicker-controls .view-switch {
  pointer-events: none;
}

.datepicker-main .datepicker-cell.next:not(.disabled),
.datepicker-main .datepicker-cell.prev:not(.disabled) {
  pointer-events: none;
}
